<template>
  <div class="article">
    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0 search"
      placeholder="搜索" v-model="keywords"
    ></b-input>
    <div class="content" v-for="post in search(keywords)">
      <router-link :to="'/hotarticle/'+'@'+post.author+'/'+post.permlink">{{ post.title }}　

        <p class="posttime">@{{ post.author }} 　{{ post.created  | formatTime}}</p>

      </router-link>
    </div>

    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>

    <div class="btn">
      <b-button variant="outline-primary" @click="getNext">查看更多</b-button>
    </div>
  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "Hot",
      data(){
          return{
            keywords:'',
            posts:[],
            author:'',
            permlink:'',
            isLoading: true
          }
      },
      methods:{
        search(keywords){
          let posts = this.posts
          return posts.filter(item => {
            if(item.title.includes(keywords)){
              // console.log(166, '查到了！')
              return item
            }
          })
        },

        getHot(){
            let that = this
            let query = { tag: "cn", limit : 20 }
            this.steem.api.getDiscussionsByTrending(query, function(err, data) {
              that.posts = data
              //取到最后一篇文章的author和Permlink，做为下一次查询的起始点
              that.author = data[data.length - 1].author
              that.permlink = data[data.length - 1].permlink
              //将得到的数据存入vuex中
              that.$store.commit('saveHot', data)
              that.isLoading = false
            })
          },

        getNext(){
          let that = this
          that.isLoading = true
          let query = {tag: "cn", limit: 20, start_author: this.author, start_permlink: this.permlink}
          this.steem.api.getDiscussionsByTrending(query, function(err, data) {
            data.forEach(post => {
              if (post.permlink != that.permlink) {
                that.posts.push(post)
              }
            })
            //取到最后一篇文章的author和Permlink，做为下一次查询的起始点
            that.author = data[data.length - 1].author
            that.permlink = data[data.length - 1].permlink
            //将得到的数据存入vuex中
            that.$store.commit('saveHot', that.posts)
            that.isLoading = false
            if(data.length == 1){
              alert('没有更多了！')
            }
          })
        }
      },

      components: {
        Loading
      },

      mounted() {
        //如果vuex中有值，则直接取出，否则从steem网络中取
        if(this.$store.state.hot != false){
          this.posts = this.$store.state.hot
          this.isLoading = false
        }else{
          this.getHot()
        }
      },

      filters:{
        formatTime(data){
          return data.substring(0,10)
        }
      },
    }
</script>

