<template>
  <div class="login">
    <h2>用户登录</h2>
    <div >
        <b-input
        id="inline-form-input-name"
        class="mb-2 mr-sm-2 mb-sm-0 user"
        placeholder="请输入用户名" v-model="username"
      ></b-input>
      <b-input
        id="inline-form-input-password"
        type="password"
        class="mb-2 mr-sm-2 mb-sm-0 user"
        placeholder="请输入发贴密钥（posting key）" v-model="password"
      ></b-input>

      <div class="loginbtn">
      <b-button variant="outline-primary" @click="login">登录</b-button>
      </div>


        <!--加载动画-->
        <transition name="fade">
          <loading v-if="isLoading"></loading>
        </transition>


    </div>
  </div>
</template>

<script>
  import Loading from './Loading'
  export default {
    name: "Login",
    data() {
      return {
        username:'',
        password:'',
        isLoading: false
      }
    },
    methods:{
      login(){
        this.isLoading = true
        let that = this
        let account = this.username
        this.steem.api.getAccountHistory(account, 0, 0, function(err, result) {
          console.log(333, err, result)
          if(result.length === 0){
            that.isLoading = false
            alert("用户不存在！")
          }
        })

        this.steem.api.getAccounts([that.username], function(err, result) {
          let pubWif = result[0].posting.key_auths[0][0]
          // console.log(111, pubWif)
          let privWif = that.password
          let isvalid
          try{
            isvalid = that.steem.auth.wifIsValid(privWif, pubWif);
            // console.log(123, isvalid)
          }
          catch(e){
            isvalid = 'false';
          }

          if(isvalid === true){
            that.isLoading = false
            // console.log(' Welcome.')
            that.$store.commit('saveUser', {username: that.username, password: privWif})
            that.$router.push({path:'/'})
          }else{
            alert(`错误！请检查用户名和发贴密钥！`)
          }
        })
      },

    },

    components: {
      Loading
    },


  }
</script>

<style scoped>

  .login{
    margin: 4rem 4rem 18rem 4rem;

  }
  .user{
    margin-top: 1.5rem;
    width: 50%;
  }
  .loginbtn{
    margin-top: 1.5rem;
  }
</style>

